import { useCounter } from "./counter-context";
import "./styles.css";

export default function App() {
  const { state, dispatch } = useCounter();

  return (
    <div className="App">
      <h1> {state.counter} </h1>
      <button onClick={() => dispatch({ type: "increment", payload: 42 })}>
        {" "}
        +{" "}
      </button>

      <button
        onClick={() => {
          dispatch({ type: "decrement", payload: 10 });
        }}
      >
        {" "}
        -{" "}
      </button>
    </div>
  );
}
